import {expectationsData} from "../mockData/expectationsData";

export const Image = ({ Image: {alt, src} }) => (
    <img src={src} alt={alt} />
);

export const First_text = ({ text }) => (
    <p className="first_text">{text}</p>
);
export const Expectations_text = ({ expectations_text }) => (
    <h2 className="expectations_text">{expectations_text}</h2>
);

export const Main_text = ({ main_text }) => (
    <p className="main_text">{main_text}</p>
);


const Expectations = () => {
    const {Image, text, expectations_text, main_text} = expectationsData;

    return (
        <>
            <div className="expectations_left">
                <Image Image={Image} />
            </div>
            <div className="expectations_right">
                <First_text text={text}/>
                <Expectations_text expectations_text={expectations_text}/>
                <Main_text main_text={main_text}/>
                <a href="#" class="request_early_access">Запросить ранний доступ</a>
            </div>
        </>
    );
};

export default Expectations;
